import { Injectable } from '@angular/core';
import { HttpResponse, HttpErrorResponse } from '@angular/common/http';
import { Observable, of, BehaviorSubject } from 'rxjs';
import { catchError, tap, pluck } from 'rxjs/operators';

import { ConsentsHttpService, ConsentListItem as ConsentItem } from '@backbase/data-ang/consent';

import {
  parseError,
  ConsentListError,
  ConsentListAccessDenied,
  ConsentListBadRequest,
  ConsentListUnknownError,
  ConsentListConnectivityError,
} from './consent-list-error';

export { ConsentListItem as ConsentItem } from '@backbase/data-ang/consent';

@Injectable()
export class ConsentListService {
  constructor(private readonly consentHttpService: ConsentsHttpService) {}

  readonly loading = new BehaviorSubject<boolean>(true);
  readonly error = new BehaviorSubject<
    ConsentListAccessDenied | ConsentListBadRequest | ConsentListUnknownError | ConsentListConnectivityError | undefined
  >(undefined);

  readonly consents: Observable<ConsentItem[]> = this.getConsentsList().pipe(
    tap(() => this.error.next(undefined)),
    catchError((error: ConsentListError) => {
      this.error.next(error);
      return of([]);
    }),
    tap(() => this.loading.next(false)),
  );

  private getConsentsList(): Observable<ConsentItem[]> {
    return this.consentHttpService.getConsents('response').pipe(
      pluck<HttpResponse<ConsentItem[]>, ConsentItem[]>('body'),
      catchError<ConsentItem[], never>((error: HttpErrorResponse) => {
        throw parseError(error);
      }),
    );
  }
}
