import { Component, Input, Output, EventEmitter } from '@angular/core';

import { ConsentItem } from '../consent-list-widget.service';

@Component({
  selector: 'bb-consent-list-component',
  templateUrl: './consent-list.component.html',
})
export class ConsentListComponent {
  /**
   * List of consents
   */
  @Input() consentsList: ConsentItem[] = [];
  /**
   * Emitter that fires when you select consent
   */
  @Output() selectConsent = new EventEmitter<string>();

  onConsentSelect(consentId: string) {
    this.selectConsent.emit(consentId);
  }
}
