import { Component, Input, Directive } from '@angular/core';

import { BbTemplate } from '@backbase/foundation-ang/core';

import { ConsentItem } from '../consent-list-widget.service';
import { Status } from '../constants';

@Component({
  selector: 'bb-consent-item-component',
  templateUrl: './consent-item.component.html',
})
export class ConsentItemComponent {
  /**
   * Component context
   */
  readonly hostRef: ConsentItemComponent = this;
  /**
   * The Consent item
   */
  @Input() consent: ConsentItem | undefined;
  /**
   * Consent item status enumeration
   */
  readonly Status = Status;
}

@Directive({
  selector: '[bbConsentListItemCustomizable]',
})
export class ConsentListItemCustomizableDirective extends BbTemplate<ConsentItemComponent, ConsentItem> {}
