import { HttpErrorResponse } from '@angular/common/http';

export class ConsentListAccessDenied {
  kind: 'ConsentListAccessDenied' = 'ConsentListAccessDenied';
  constructor(readonly error: HttpErrorResponse) {}
}

export class ConsentListBadRequest {
  kind: 'ConsentListBadRequest' = 'ConsentListBadRequest';
  constructor(readonly error: HttpErrorResponse) {}
}

export class ConsentListUnknownError {
  kind: 'ConsentListUnknownError' = 'ConsentListUnknownError';
  constructor(readonly error: HttpErrorResponse) {}
}

export class ConsentListConnectivityError {
  kind: 'ConsentListConnectivityError' = 'ConsentListConnectivityError';
  constructor(readonly error: HttpErrorResponse) {}
}

export type ConsentListError =
  | ConsentListAccessDenied
  | ConsentListBadRequest
  | ConsentListUnknownError
  | ConsentListConnectivityError;

export const parseError = (error: HttpErrorResponse): ConsentListError => {
  if (error.error instanceof ErrorEvent) {
    return new ConsentListUnknownError(error);
  } else {
    switch (error.status) {
      case 0:
        return new ConsentListConnectivityError(error);
      case 400:
        return new ConsentListBadRequest(error);
      case 401:
        return new ConsentListAccessDenied(error);
      case 403:
        return new ConsentListAccessDenied(error);
      default:
        return new ConsentListUnknownError(error);
    }
  }
};
