import { Component, Directive, Output, EventEmitter } from '@angular/core';
import { BbTemplate } from '@backbase/foundation-ang/core';
import { Observable } from 'rxjs';

import { ConsentListService, ConsentItem } from './consent-list-widget.service';
import {
  ConsentListAccessDenied,
  ConsentListBadRequest,
  ConsentListConnectivityError,
  ConsentListUnknownError,
} from './consent-list-error';

@Component({
  selector: 'bb-consent-list-widget',
  templateUrl: './consent-list-widget.component.html',
})
export class ConsentListWidgetComponent {
  /**
   * Component context
   */
  readonly hostRef: ConsentListWidgetComponent = this;
  /**
   * Emitter that is fired when selecting consent
   */
  @Output() consentId: EventEmitter<string> = new EventEmitter<string>();
  /**
   * Stream of loading spinner visibility flag
   */
  readonly loading: Observable<boolean> = this.service.loading;
  /**
   * Stream of Consent list errors
   */
  readonly error: Observable<
    ConsentListAccessDenied | ConsentListBadRequest | ConsentListUnknownError | ConsentListConnectivityError | undefined
  > = this.service.error;
  /**
   * Stream of consent items
   */
  readonly consents: Observable<ConsentItem[] | undefined> = this.service.consents;
  /**
   * Select consent click event handler.
   * @param consentId - consent ID
   */
  selectConsent(consentId: string): void {
    this.consentId.emit(consentId);
  }

  constructor(private readonly service: ConsentListService) {}
}

@Directive({
  selector: '[bbConsentListWidgetCustomizable]',
})
export class ConsentListWidgetCustomizableDirective extends BbTemplate<
  ConsentListWidgetComponent,
  ConsentItem[] | null
> {}
